import React, { useState, useRef, useEffect } from 'react';
import axios from 'axios';
import isMobile from 'is-mobile';

import './index.scss';

interface IProps {
  floor: number;
  onChangeFloor: (delta: number) => void;
  token: string;
  isUnitAvailable: (unitName: string) => boolean;
  onUnitNameClicked: (unitName: string) => void;
  onClose: () => void;
  widthFallback: boolean;
  labelEnterCallback: (unitName: string) => void;
  labelLeaveCallback: () => void;
  assetStoreUrl: string;
}

type Props = IProps;

const FloorSwitcher: React.SFC<Props> = ({
  floor,
  onChangeFloor,
  token,
  isUnitAvailable,
  onUnitNameClicked,
  onClose,
  widthFallback,
  labelEnterCallback,
  labelLeaveCallback,
  assetStoreUrl,
}) => {
  const [keyplanCache, updateKeyplanCache] = useState<{
    [floor: number]: string;
  }>({});
  const containerRef = useRef();

  const updateKeyplan = (xmlString: string) => {
    const container: HTMLDivElement = containerRef.current;
    const parser = new DOMParser();
    const doc = parser.parseFromString(xmlString, 'image/svg+xml');

    const paths = doc.getElementsByTagNameNS(
      'http://www.w3.org/2000/svg',
      'path'
    );

    Array.from(paths).forEach((path) => {
      const unitName = path.id.replace('_', `${floor}`);
      const available = isUnitAvailable(unitName);

      path.addEventListener('click', () => {
        onUnitNameClicked(unitName);
      });

      if (!isMobile({ tablet: true })) {
        path.addEventListener('mouseenter', () => {
          labelEnterCallback(unitName);
        });

        path.addEventListener('mouseleave', () => {
          labelLeaveCallback();
        });
      }

      if (available) {
        path.classList.add('available');
      }
    });

    if (container) {
      container.innerHTML = '';
      container.appendChild(doc.documentElement);
    }
  };

  useEffect(() => {
    if (keyplanCache[floor]) {
      const data = keyplanCache[floor];
      updateKeyplan(data);
    } else {
      axios
        .get(`${assetStoreUrl}/${token}/360/keyplans/${floor}.svg`, {
          headers: { 'Content-Type': 'image/svg+xml' },
        })
        .then((response) => {
          const data = response.data;
          updateKeyplan(data);

          updateKeyplanCache(Object.assign(keyplanCache, { floor: data }));
        });
    }
  }, [floor]);

  const incrementFloor = () => onChangeFloor(1);
  const decrementFloor = () => onChangeFloor(-1);

  return (
    <div className="ev-floor-switcher">
      {!widthFallback && (
        <button onClick={onClose} className="ev-floor-switcher-close">
          &times;
        </button>
      )}
      <div className="ev-floor-control">
        <button className="ev-change-floor-btn" onClick={incrementFloor}>
          &#9658;
        </button>
        <span className="ev-floor-label">{floor}</span>
        <button className="ev-change-floor-btn" onClick={decrementFloor}>
          &#9658;
        </button>
      </div>
      <div className="ev-svg-container" ref={containerRef}></div>
    </div>
  );
};

export default FloorSwitcher;
