import { formatInteger, getDivisors } from "./utils";

export function getImageSequenceUrls(
    baseUrl: string,
    imageCount: number,
    extension: string = "jpg",
    step: number = 1
): string[] {

    const result: string[] = [];

    for (let i = 0; i < imageCount; i += step) {
        result.push(`${baseUrl}/${formatInteger(i)}.${extension}`.replace(/\/\//g, "/"));
    }

    return result;
}

export function getImageLoadingIndexSequences(count: number, initialIndex: number = 0): number[][] {
    const divisors = getDivisors(count);

    const result = [];
    for (let i = 0; i < divisors.length; i++) {
        result.push(getImageIndexSequenceForDivisor(count, divisors[i], initialIndex));
    }

    return result;
}

export function getImageIndexSequenceForDivisor(count: number, divisor: number, initialIndex: number = 0): number[] {
    if (count % divisor) {
        throw "[getImageIndexSequence]: Invalid divisor";
    }

    const delta = count / divisor;
    const result = [];
    let current = initialIndex;
    for (let i = 0; i < divisor; i++) {
        result.push(current);
        current = (current + delta) % count;
    }

    return result;
}

export function indexSequenceCountUniqueInidices(imageLoadingSequences: number[][]): number {
    const indices = [];
    let count = 0;

    for (let i = 0; i < imageLoadingSequences.length; i++) {
        let currentSequence = imageLoadingSequences[i];

        for (let j = 0; j < currentSequence.length; j++) {
            let currentIndex = currentSequence[j];

            if (!indices[currentIndex]) {
                indices[currentIndex] = true;
                ++count;
            }
        }
    }

    return count;
}
