import React, { useContext } from 'react';

import './index.scss';
import { ITranslations } from 'src';
import { TranslationsContext } from '../App';

interface IProps {
  shouldAnchorTop: boolean;
}

type Props = IProps;

const Tutorial: React.SFC<Props> = (props: Props) => {
  const translations: ITranslations = useContext(TranslationsContext);
  //console.log(translations.helpText);
  const { shouldAnchorTop } = props;
  return (
    <div className={`ev-legend-container ${shouldAnchorTop && 'top'}`}>
      <button className="ev-legend-btn">HELP</button>
      <div className="ev-legend-tooltip">
        <h2>User Tips</h2>
        <hr />
        {translations.helpText.map((el: any) => {
          return <p className="ev-legend-contents">{el}</p>;
        })}
      </div>
    </div>
  );
};

export default Tutorial;
