import {
    SageSvgHotspotImageProperties,
    SageNodeController,
    SageNodeType
} from "./types";

export function svgHotspotImageControllerFactory(props: SageSvgHotspotImageProperties): SageNodeController {

    const mount = (container, navigateToNode) => {
        fetch(props.url)
            .then(res => res.text())
            .then(text => { container.innerHTML = text; })
            .then(() => {
                console.log(container.getElementsByTagName("svg"));

                const svg = container.getElementsByTagName("svg")[0];

                svg.setAttribute("width", container.clientWidth.toString());
                svg.setAttribute("height", container.clientHeight.toString());

                if (props.navigation) {
                    const keys = Object.keys(props.navigation);
                    for (let i = 0; i < keys.length; i++) {
                        const elementId = keys[i];
                        const targetNodeId = props.navigation[elementId];

                        const element = svg.getElementById(elementId);

                        if (element) {
                            element.setAttribute("pointer-events", "visible");
                            element.addEventListener("click", () => {
                                navigateToNode(targetNodeId);
                            });
                        }
                    }
                }
            });
    };

    return ({
        init: () => { },
        mount,
        getNodeType: () => SageNodeType.SvgHotspotImageNode
    });
}
