export async function orbitReelCreateFromUrsula(
  container: HTMLDivElement,
  token: string,
  options?: IOrbitReelOptions,
  customOptions?: ICustomOptions
) {
  const loadAsync = async () => {
    const buildingData = await axios.get(
      `https://ursula.adminsage.com/api/getBuildingData?token=${token}`
    );

    const buildingPlans = await axios.get(
      `https://ursula.adminsage.com/api/getUnitPlans?token=${token}`
    );
    const buildingUnits = await axios.get(
      `https://ursula.adminsage.com/api/getUnits?token=${token}`
    );

    const unitMedia = buildingUnits.data.Units.map((unit) => unit.files);

    const unitPlans = buildingPlans.data.UnitPlans.map((plan, index) => {
      return { ...plan, gallery: unitMedia[index] };
    });

    let processedUnits = [];
    let processedBuilding = undefined;

    if (buildingData.status === 200) {
      const {
        data: {
          data: { units, building },
        },
      } = buildingData;

      processedBuilding = { ...building };
      processedUnits = Object.keys(units).map((key) => {
        const unit = units[key];
        let {
          number,
          bedrooms,
          bathrooms,
          status,
          floor,
          planId,
          externalPrice,
        } = unit;
        bedrooms = Number(bedrooms);
        bathrooms = Number(bathrooms);
        floor = Number(floor);

        const unitPlan = unitPlans.find((plan) => plan.id == planId);
        const { gallery } = unitPlan;

        const files =
          unitPlan != null
            ? unitPlan.files
            : ['https://ursula.adminsage.com/img/missing_plan.jpg'];
        const SpecialFeatures =
          unitPlan != null ? unitPlan.SpecialFeatures : 'Plan Missing';
        let SquareFeets =
          unitPlan != null ? unitPlan.SquareFeets : unit.squareFeet;
        let BalconySize = unitPlan != null ? unitPlan.BalconySize : 0;

        //const { files, SpecialFeatures } = unitPlan;
        //let { SquareFeets, BalconySize } = unitPlan;
        SquareFeets = Number(SquareFeets);
        BalconySize = Number(BalconySize);

        const interiorSquareFeet = SquareFeets;
        const exteriorSquareFeet = BalconySize;
        const totalSquareFeet = SquareFeets + BalconySize;

        return {
          name: number,
          bedrooms,
          bathrooms,
          floor,
          isUnavailable: status !== 'Available',
          status,
          floorplanAssetsSrc: files,
          floorplanName: SpecialFeatures,
          price: Number(externalPrice),
          interiorSquareFeet,
          exteriorSquareFeet,
          gallery,
        };
      });
    }

    const receiveUnitsEvent = new CustomEvent('EV_EVT--RECEIVE_UNITS', {
      detail: processedUnits,
    });

    const receiveBuildingEvent = new CustomEvent('EV_EVT--RECEIVE_BUILDING', {
      detail: processedBuilding,
    });

    document.dispatchEvent(receiveUnitsEvent);
    document.dispatchEvent(receiveBuildingEvent);
  };

  loadAsync();

  return orbitReelFromToken(
    container,
    token,
    {
      units: [],
      ...options,
    },
    customOptions
  );
}

export async function orbitReelFromToken(
  container: HTMLDivElement,
  token: string,
  externalOptions: IOrbitReelOptions,
  customOptions?: ICustomOptions
) {
  const handleResize = () => {
    container.style.width = `${window.innerWidth}px`;
    container.style.height = `${window.innerHeight}px`;
  };
  window.addEventListener('orientationchange', () => {
    setTimeout(handleResize, 200);
  });
  window.addEventListener('resize', handleResize);
  handleResize();
  ReactDOM.render(
    <App
      token={token}
      externalOptions={{
        ...externalOptions,
        units: externalOptions.units ? externalOptions.units : [],
      }}
      customOptions={customOptions}
    />,
    container
  );

  let isReelReady = false;
  let eventQueue = [];

  document.addEventListener('EV_EVT--REEL_READY', () => {
    isReelReady = true;

    if (eventQueue.length > 0) {
      const latestEvent = eventQueue.pop();
      document.dispatchEvent(latestEvent);
    }
  });

  return {
    overrideFilter: (filter: (unit: IUnit) => boolean) => {
      const overrideFilterEvent = new CustomEvent('EV_EVT--OVERRIDE_FILTER', {
        detail: filter,
      });

      if (!isReelReady) {
        eventQueue.push(overrideFilterEvent);
      } else {
        document.dispatchEvent(overrideFilterEvent);
      }
    },
  };
}
