import React, { useState, useEffect, useContext } from 'react';

import './index.scss';
import { ITransition, TranslationsContext } from '../App';
import { IUnit } from 'src/orbit-reel';
import { ITranslations, IActionBarOptions } from 'src';

interface IProps {
  transitions: ITransition[];
  onFilterChange: (filter: (unit: IUnit) => boolean) => void;
  activeTransition: string;
  onTransition: (transition: string) => void;
  shouldAnchorTop: boolean;
  onModeChange: (mode: 'units' | 'amenities') => void;
  isTransitioning: boolean;
  options: IActionBarOptions;
}

type Props = IProps;

const ActionBar = (props: Props) => {
  const translations: ITranslations = useContext(TranslationsContext);

  const {
    transitions,
    onFilterChange,
    activeTransition,
    onTransition,
    shouldAnchorTop,
    onModeChange,
    isTransitioning,
    options,
  } = props;

  const actionBarOptions: IActionBarOptions = Object.assign(
    {
      disableBathsFilter: false,
      disableBedsFilter: false,
      bedsOptions: [1, 2, 3, 4],
      bathsOptions: [1, 2, 3, 4],
    },
    options
  );

  const maxBedOption = Math.max(
    ...actionBarOptions.bedsOptions.map((c) => Number(c))
  );
  const maxBathOption = Math.max(
    ...actionBarOptions.bathsOptions.map((c) => Number(c))
  );

  const [isDropped, setDropped] = useState<boolean>(false);
  const [bedrooms, setBedrooms] = useState<string[]>(['all']);
  const [bathrooms, setBathrooms] = useState<string[]>(['all']);
  const [unitQuery, setUnitQuery] = useState<string>('');

  const [mode, setMode] = useState<'units' | 'amenities'>('units');

  const transitionsDisabled = !transitions || transitions.length === 0;
  const searchDisabled = isTransitioning;

  useEffect(() => {
    onModeChange(mode);
  }, [mode]);

  useEffect(() => {
    const unitFilter = (unit: IUnit): boolean => {
      const matchesBedsBaths = (
        bedsBaths: number,
        choices: string[]
      ): boolean => {
        const choicesAsFns = choices.map((choice) => {
          const asNumeric = Number(choice);
          if (!isNaN(asNumeric)) {
            return (bedsBath: number) =>
              bedsBath === asNumeric ||
              (asNumeric === maxBedOption && bedsBath >= asNumeric);
          }

          return (bedsBaths: number) => false;
        });

        if (choicesAsFns.map((fn) => fn(bedsBaths)).includes(true)) {
          return true;
        }

        return choices.includes('all');
      };

      return (
        matchesBedsBaths(unit.bedrooms, bedrooms) &&
        matchesBedsBaths(unit.bathrooms, bathrooms) &&
        (unit.name.toLowerCase().includes(unitQuery.toLowerCase()) || unit.floorplanName.toLowerCase().includes(unitQuery.toLowerCase()))
      );
    };

    onFilterChange(unitFilter);
  }, [bedrooms, bathrooms, unitQuery]);

  const setSearchMode = () => {
    if (!searchDisabled) {
      setMode('units');
    }
  };

  const setAmenitiesMode = () => {
    if (!transitionsDisabled) {
      setMode('amenities');
    }
  };

  const updateBedsBaths = (option: string, current: string[]): string[] => {
    let updated = [...current];

    updated = updated.filter((o) => o !== 'all');

    if (updated.includes(option)) {
      updated = updated.filter((o) => o !== option);
    } else {
      updated.push(option);
    }

    if (updated.length === 0) {
      updated.push('all');
    }

    if (updated.includes('all')) {
      updated = ['all'];
    }

    return updated;
  };

  const updateBathrooms = (option: string) => {
    setBathrooms(updateBedsBaths(option, bathrooms));
  };

  const updateBedrooms = (option: string) => {
    setBedrooms(updateBedsBaths(option, bedrooms));
  };

  const updateUnitQuery = (evt: React.ChangeEvent<HTMLInputElement>) => {
    const {
      target: { value },
    } = evt;

    setUnitQuery(value);
  };

  return (
    <div
      className={`ev-action-bar-container ${shouldAnchorTop ? 'top' : ''}`}
      style={{
        transform: isDropped
          ? `translate(-50%, ${shouldAnchorTop ? '-' : ''}112%)`
          : 'translate(-50%, 0%)',
      }}>
      <div
        className={`ev-lab-dropdown-container ${shouldAnchorTop ? 'top' : ''}`}
        onClick={() => setDropped(!isDropped)}>
        <img
          src={B64_ARROW_DOWN}
          style={{
            transform: isDropped
              ? `rotate(${shouldAnchorTop ? '0' : '180'}deg)`
              : `rotate(${shouldAnchorTop ? '180' : '0'}deg)`,
          }}
          alt="Dropdown"
        />
      </div>
      <div className="ev-mode-buttons ev-filter-group">
        <button
          onClick={setSearchMode}
          className={`${mode === 'units' && 'active'} ${
            searchDisabled ? 'disabled' : ''
          }`}>
          {translations.unitSearch}
        </button>
        <button
          onClick={setAmenitiesMode}
          className={`${mode === 'amenities' ? 'active' : ''} ${
            transitionsDisabled ? 'disabled' : ''
          }`}>
          {translations.buildingsAmenities}
        </button>
      </div>
      {mode === 'units' && (
        <>
          {!actionBarOptions.disableBedsFilter && (
            <div className="ev-bedrooms-container ev-filter-group">
              <span>Bedrooms</span>
              <div className="ev-options-container">
                {actionBarOptions.bedsOptions.map((option) => {
                  return (
                    <button
                      className={`ev-option ${
                        bedrooms.includes(`${option}`) && 'active'
                      }`}
                      onClick={() => updateBedrooms(`${option}`)}>
                      {`${option}` === `${maxBedOption}`
                        ? `${option}+`
                        : option}
                    </button>
                  );
                })}
                <button
                  className={`ev-option ${
                    bedrooms.includes('all') && 'active'
                  }`}
                  onClick={() => updateBedrooms('all')}>
                  All
                </button>
              </div>
            </div>
          )}
          {!actionBarOptions.disableBathsFilter && (
            <div className="ev-bathrooms-container ev-filter-group">
              <span>Bathrooms</span>
              <div className="ev-options-container">
                {actionBarOptions.bathsOptions.map((option) => {
                  return (
                    <button
                      className={`ev-option ${
                        bathrooms.includes(`${option}`) && 'active'
                      }`}
                      onClick={() => updateBathrooms(`${option}`)}>
                      {`${option}` === `${maxBathOption}`
                        ? `${option}+`
                        : option}
                    </button>
                  );
                })}
                <button
                  className={`ev-option ${
                    bathrooms.includes('all') && 'active'
                  }`}
                  onClick={() => updateBathrooms('all')}>
                  All
                </button>
              </div>
            </div>
          )}
          <div className="ev-unit-search ev-filter-group">
            <span>Search By Unit Name</span>
            <div className="ev-search-container">
              <input type="text" value={unitQuery} onChange={updateUnitQuery} />
              <img src={B64_SEARCH_ICON} alt="Search" />
            </div>
          </div>
        </>
      )}
      {mode === 'amenities' && (
        <>
          <div className="ev-transitions-container">
            <button
              key="all"
              className={`ev-transition-button ${
                activeTransition === '' && 'active'
              }`}
              onClick={() => onTransition('')}>
              {translations.viewAll}
            </button>
            {transitions.map((transition) => (
              <button
                key={transition.name}
                className={`ev-transition-button ${
                  activeTransition === transition.name && 'active'
                }`}
                onClick={() => onTransition(transition.name)}>
                {transition.label}
              </button>
            ))}
          </div>
        </>
      )}
    </div>
  );
};

export const B64_SEARCH_ICON = `data:image/png;base64,
`;

export const B64_ARROW_DOWN = `data:image/png;base64,
`;

export default ActionBar;
