import React, { useState, useEffect } from 'react';
import './index.scss';
import { IUnitOverlayFields, IUnitOverlayFieldTitles } from 'src';
import { B64_CLOSE } from '../HelpPopup';
import { IUnit } from 'src/orbit-reel';
import styled from 'styled-components';
import { B64_KB_ARROW_LEFT } from '../UnitOverlay';
import { numberWithCommas } from '../../utils/index';

import MediaGallery from '../media-gallery';
interface IProps {
  onClose: () => void;
  onExpandImage: (src: string) => void;
  unitOverlayFields: IUnitOverlayFields;
  unitOverlayFieldTitles: IUnitOverlayFieldTitles;
  visible: boolean;
  unit: IUnit;
  assetStore: string;
  token: string;
  hasUnitViews: boolean;
}

type Props = IProps;

const UnitOverlayPortrait = (props: Props) => {
  const {
    unit,
    onClose,
    onExpandImage,
    unitOverlayFields,
    visible,
    assetStore,
    token,
    hasUnitViews,
    unitOverlayFieldTitles,
  } = props;

  const {
    name,
    status,
    bedrooms,
    bathrooms,
    interiorSquareFeet,
    exteriorSquareFeet,
    isUnavailable,
    floorplanAssetsSrc,
    price,
  } = unit
    ? unit
    : {
        name: '',
        status: '',
        bedrooms: 0,
        bathrooms: 0,
        interiorSquareFeet: 0,
        exteriorSquareFeet: 0,
        isUnavailable: true,
        floorplanAssetsSrc: [''],
        price: 0,
      };

  const gallery = unit ? (unit as any).gallery || [] : [];

  const processedGallery = gallery.map((src) => {
    return {
      src,
      thumbnailSrc: src,
      category: 'all',
      tip: '',
      isExternal: true,
    };
  });

  const available = !isUnavailable;
  const floorplanSrc = floorplanAssetsSrc[0];
  const [isShowingGallery, setShowingGallery] = useState<boolean>(false);

  const openGallery = () => {
    setShowingGallery(true);
  };

  const closeGallery = () => {
    setShowingGallery(false);
  };

  return (
    <div
      className="ev-unit-overlay-portrait-container"
      style={{
        right: visible ? '0' : '-94%',
        visibility: visible ? 'visible' : 'hidden',
      }}
    >
      <header>
        {isShowingGallery ? (
          <button
            className="ev-portrait-back-unit-overlay"
            onClick={closeGallery}
          >
            <img src={B64_KB_ARROW_LEFT} />
            <span>Back</span>
          </button>
        ) : (
          <>
            {hasUnitViews ? (
              <button className="ev-portrait-gallery-btn" onClick={openGallery}>
                Image Gallery
              </button>
            ) : (
              <div className="ev-portrait-gallery-spacer"></div>
            )}
          </>
        )}
        <button onClick={onClose}>
          <img src={B64_CLOSE} alt="Close" />
        </button>
      </header>
      {isShowingGallery && visible ? (
        <GalleryWrapper>
          <MediaGallery
            assetStore={assetStore}
            token={token}
            gallery={processedGallery}
            category="all"
            onExpandImageFullscreen={onExpandImage}
            containIterators={true}
            thumbnailsOnBottom={true}
          />
        </GalleryWrapper>
      ) : (
        <>
          <div className="ev-portrait-unit-gallery-container">
            {visible && (
              <img
                src={floorplanSrc}
                alt="Unit Floorplan"
                onClick={() => onExpandImage(floorplanSrc)}
              />
            )}
          </div>
          <div className="ev-portrait-unit-details-container">
            <h2 className="ev-portrait-unit-name">{name}</h2>

            {!unitOverlayFields.disableStatus && (
              <div className="ev-portrait-unit-detail">
                <div>
                  {unitOverlayFieldTitles.statusTitle
                    ? unitOverlayFieldTitles.statusTitle
                    : 'Status'}
                </div>
                <span>{status}</span>
              </div>
            )}

            {!unitOverlayFields.disableBedrooms && (
              <div className="ev-portrait-unit-detail">
                <div>
                  {unitOverlayFieldTitles.bedroomTitle
                    ? unitOverlayFieldTitles.bedroomTitle
                    : 'Bedrooms'}
                </div>
                <span>{bedrooms}</span>
              </div>
            )}

            {!unitOverlayFields.disableBathrooms && (
              <div className="ev-portrait-unit-detail">
                <div>
                  {unitOverlayFieldTitles.bathroomTitle
                    ? unitOverlayFieldTitles.bathroomTitle
                    : 'Bathrooms'}
                </div>
                <span>{bathrooms}</span>
              </div>
            )}

            {!unitOverlayFields.disableInteriorSize && (
              <div className="ev-portrait-unit-detail">
                <div>
                  {unitOverlayFieldTitles.interiorSizeTitle
                    ? unitOverlayFieldTitles.interiorSizeTitle
                    : 'Interior Size'}
                </div>
                <span>{numberWithCommas(interiorSquareFeet)} sq ft</span>
              </div>
            )}
            {!unitOverlayFields.disableExteriorSize && (
              <div className="ev-portrait-unit-detail">
                <div>
                  {unitOverlayFieldTitles.exteriorSizeTitle
                    ? unitOverlayFieldTitles.exteriorSizeTitle
                    : 'Exterior Size'}
                </div>
                <span>{numberWithCommas(exteriorSquareFeet)} sq ft</span>
              </div>
            )}
            {!unitOverlayFields.disableAvailability && (
              <div className="ev-portrait-unit-detail">
                <div>
                  {unitOverlayFieldTitles.availabilityTitle
                    ? unitOverlayFieldTitles.availabilityTitle
                    : 'Availability'}
                </div>
                <span>{available ? 'Available Now' : 'Unavailable'}</span>
              </div>
            )}

            {!unitOverlayFields.disablePricing && (
              <div className="ev-portrait-unit-detail">
                <div>
                  {unitOverlayFieldTitles.pricingTitle
                    ? unitOverlayFieldTitles.pricingTitle
                    : 'Pricing'}
                </div>
                <span>
                  {price && price > 0
                    ? `$${numberWithCommas(price)}`
                    : 'Call for Pricing'}
                </span>
              </div>
            )}
          </div>
        </>
      )}
    </div>
  );
};

const GalleryWrapper = styled.div`
  width: 100%;
  height: calc(100% - 54px);
  padding: 10px 5px;
  box-sizing: border-box;
`;

export default UnitOverlayPortrait;
