import React, { useEffect, useRef, useState } from 'react';
import './index.scss';
import { IUnitOverlayFields, IUnitOverlayFieldTitles } from 'src';
import { IUnit } from 'src/orbit-reel';

import MediaGallery from '../media-gallery';
import { numberWithCommas } from '../../utils/index';

interface IProps {
  unit: IUnit;
  onClose: () => void;
  onExpandImage: (src: string) => void;
  unitOverlayFields: IUnitOverlayFields;
  assetStore: string;
  token: string;
  fontSize: number;
  hasUnitViews: boolean;
  unitOverlayFieldTitles: IUnitOverlayFieldTitles;
}

type Props = IProps;

const CLOSE_BUTTON_B64 = `data:image/png;base64,`;

const UnitOverlay: React.SFC<Props> = ({
  onClose,
  onExpandImage,
  unitOverlayFields,
  unit,
  assetStore,
  token,
  hasUnitViews,
  fontSize,
  unitOverlayFieldTitles,
}) => {
  const {
    name,
    status,
    bedrooms,
    bathrooms,
    interiorSquareFeet,
    exteriorSquareFeet,
    price,
    isUnavailable,
    floorplanAssetsSrc,
  } = unit;
  let { gallery } = unit as any;
  gallery = gallery ? gallery : [];

  const available = !isUnavailable;
  const floorplanSrc = floorplanAssetsSrc[0];

  const [isShowingGallery, setShowingGallery] = useState<boolean>(false);

  const processedGallery = gallery.map((src) => {
    return {
      src,
      thumbnailSrc: src,
      category: 'all',
      tip: '',
      isExternal: true,
    };
  });

  const openGallery = () => {
    setShowingGallery(true);
  };

  const closeGallery = () => {
    setShowingGallery(false);
  };

  return (
    <div className={`ev-unit-overlay-container`}>
      <div className="ev-unit-overlay-outer">
        <div className="ev-unit-overlay-inner">
          <button className="ev-close-unit-overlay" onClick={onClose}>
            <img src={CLOSE_BUTTON_B64} />
          </button>
          {isShowingGallery ? (
            <>
              <button className="ev-back-unit-overlay" onClick={closeGallery}>
                <img src={B64_KB_ARROW_LEFT} />
                <span>Back</span>
              </button>
              <div className="ev-gallery-wrapper">
                <div className="ev-gallery-btn-container">
                  <MediaGallery
                    assetStore={assetStore}
                    token={token}
                    gallery={processedGallery}
                    category="all"
                    onExpandImageFullscreen={onExpandImage}
                    containIterators={true}
                  />
                </div>
              </div>
            </>
          ) : (
            <>
              <div className="ev-gallery-container">
                <img
                  src={floorplanSrc}
                  onClick={() => onExpandImage(floorplanSrc)}
                />
                <button onClick={() => onExpandImage(floorplanSrc)}>
                  <img src={B64_FULLSCREEN} alt="Fullscreen" />
                  <span>Full Screen</span>
                </button>
              </div>
              <div className="ev-unit-detail-container" style={{ fontSize }}>
                <h1 className="ev-unit-detail-name">{name}</h1>
                {!unitOverlayFields.disableStatus && (
                  <div className="ev-unit-detail">
                    <div>
                      {unitOverlayFieldTitles.statusTitle
                        ? unitOverlayFieldTitles.statusTitle
                        : 'Status'}
                    </div>
                    <span>{status}</span>
                  </div>
                )}
                {!unitOverlayFields.disableBedrooms && (
                  <div className="ev-unit-detail">
                    <div>
                      {unitOverlayFieldTitles.bedroomTitle
                        ? unitOverlayFieldTitles.bedroomTitle
                        : 'Bedrooms'}
                    </div>
                    <span>{bedrooms}</span>
                  </div>
                )}
                {!unitOverlayFields.disableBathrooms && (
                  <div className="ev-unit-detail">
                    <div>
                      {unitOverlayFieldTitles.bathroomTitle
                        ? unitOverlayFieldTitles.bathroomTitle
                        : 'Bathrooms'}
                    </div>
                    <span>{bathrooms}</span>
                  </div>
                )}
                {!unitOverlayFields.disableInteriorSize && (
                  <div className="ev-unit-detail">
                    <div>
                      {unitOverlayFieldTitles.interiorSizeTitle
                        ? unitOverlayFieldTitles.interiorSizeTitle
                        : 'Interior Size'}
                    </div>
                    <span>{numberWithCommas(interiorSquareFeet)} sq ft</span>
                  </div>
                )}
                {!unitOverlayFields.disableExteriorSize && (
                  <div className="ev-unit-detail">
                    <div>
                      {unitOverlayFieldTitles.exteriorSizeTitle
                        ? unitOverlayFieldTitles.exteriorSizeTitle
                        : 'Exterior Size'}
                    </div>
                    <span>{numberWithCommas(exteriorSquareFeet)} sq ft</span>
                  </div>
                )}
                {!unitOverlayFields.disableAvailability && (
                  <div className="ev-unit-detail">
                    <div>
                      {unitOverlayFieldTitles.availabilityTitle
                        ? unitOverlayFieldTitles.availabilityTitle
                        : 'Availability'}
                    </div>
                    <span>{available ? 'Available Now' : 'Unavailable'}</span>
                  </div>
                )}
                {!unitOverlayFields.disablePricing && (
                  <div className="ev-unit-detail">
                    <div>
                      {unitOverlayFieldTitles.pricingTitle
                        ? unitOverlayFieldTitles.pricingTitle
                        : 'Pricing'}
                    </div>
                    <span>
                      {price && price > 0
                        ? `$${numberWithCommas(price)}`
                        : 'Call for Pricing'}
                    </span>
                  </div>
                )}
                {hasUnitViews && (
                  <div className="ev-gallery-btn-wrapper">
                    <button className="ev-gallery-btn" onClick={openGallery}>
                      View Image Gallery
                    </button>
                  </div>
                )}
              </div>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export const B64_FULLSCREEN = `data:image/png;base64,
`;

export const B64_KB_ARROW_LEFT = `data:image/png;base64,`;

export default UnitOverlay;
