import React, { useState } from 'react';

import './index.scss';

interface IProps {
  gallery: string[];
}

type Props = IProps;

const Gallery: React.SFC<Props> = ({ gallery }) => {
  const [index, setIndex] = useState<number>(0);

  const updateIndex = (delta: number) => {
    let newIndex = index + delta;
    if (newIndex >= gallery.length) {
      newIndex = 0;
    } else if (newIndex < 0) {
      newIndex = gallery.length - 1;
    }

    setIndex(newIndex);
  };

  return (
    <div className="ev-am-gallery-container">
      <div className="ev-am-gallery-image-container">
        <img src={gallery[index]} />
        <button onClick={() => updateIndex(-1)}>Previous</button>
        <button onClick={() => updateIndex(1)}>Next</button>
      </div>
    </div>
  );
};

export default Gallery;
