import React from 'react';

import './index.scss';

interface IProps {
  onClose: () => void;
  src: string;
}

type Props = IProps;

const UnitOverlayExpansion: React.SFC<Props> = ({ onClose, src }) => {
  return (
    <div className="ev-unit-overlay-expanded">
      <img src={src} />
      <button className="ev-unit-overlay-expanded-close" onClick={onClose}>
        <span>&times;</span>
      </button>
    </div>
  );
};

export default UnitOverlayExpansion;
