import React from 'react';

import './index.scss';

interface IProps {
  name: string;
  bedrooms: number;
  bathrooms: number;
  squareFeet: number;
  price: number;
  floorplanSrc: string;
  x: number;
  y: number;
  flipX: boolean;
  flipY: boolean;
  fontSize: number;
  isUnavailable: boolean;
}

type Props = IProps;

const UnitPopup: React.SFC<Props> = ({
  name,
  bedrooms,
  bathrooms,
  squareFeet,
  price,
  isUnavailable,
  floorplanSrc,
  x,
  y,
  flipX,
  flipY,
  fontSize,
}) => {
  const transform = `translate(${flipX ? '-100%' : '0%'}, ${
    flipY ? '-100%' : '0%'
  })`;

  return (
    <div
      className="ev-unit-popup"
      style={{ left: x, top: y, transform, fontSize: `${fontSize}px` }}>
      <h2 className="ev-unit-popup-title">{name}</h2>
      <div className="ev-unit-brbasqft">
        {bedrooms} BD | {bathrooms} BA | {Number(squareFeet).toLocaleString()}{' '}
        SQ FT
      </div>
      <div className="ev-unit-popup-ls"></div>
      <div className="ev-unit-fp-container">
        <img
          className="ev-unit-fp"
          src={floorplanSrc}
          alt="Unit Floorplan Image"
        />
      </div>
    </div>
  );
};

export default UnitPopup;
