import React, { useContext } from 'react';

import './index.scss';
import { TranslationsContext } from '../App';
import { ITranslations } from 'src';

interface IProps {
  logoSrc: string;
  mode: 'floors' | 'units' | 'amenities';
  onClose: () => void;
  fontSize: number;
}

type Props = IProps;

const HelpPopup: React.SFC<Props> = ({ logoSrc, mode, fontSize, onClose }) => {
  const translations: ITranslations = useContext(TranslationsContext);

  const welcomeText = {
    floors: translations.introTextHeaderFloors,
    units: translations.introTextHeaderUnitSearch,
    amenities: translations.introTextHeaderAmenities,
  }[mode];

  const helpText = {
    floors: translations.introTextFloors,
    units: translations.introTextUnitSearch,
    amenities: translations.introTextAmenities,
  }[mode];

  return (
    <div className="ev-help-popup-bg">
      <div className="ev-help-popup" style={{ fontSize: `${fontSize}px` }}>
        <button onClick={onClose} className="close-btn">
          <img src={B64_CLOSE} alt="Close" />
        </button>
        <img className="ev-brand-logo" src={logoSrc} />
        <hr className="ev-hr" />
        <p className="ev-help-welcome">{welcomeText}</p>
        {helpText.map((txt) => (
          <p className="ev-help-text">{txt}</p>
        ))}
        <button onClick={onClose} className="continue-btn">
          Continue
        </button>
      </div>
    </div>
  );
};

export const B64_CLOSE = `data:image/png;base64,
`;
export default HelpPopup;
