import * as THREE from "three";

export interface Rectangle {
    x: number;
    y: number;
    width: number;
    height: number;
}

export interface OrbitReelCameraData {
    fov: number;
    frames: number[][];
    target: number[] | number[];
}

export interface PartialLabel {
    minDistance: number;
    rectangles: {
        tr: THREE.Vector2;
        tl: THREE.Vector2;
        br: THREE.Vector2;
        bl: THREE.Vector2;
    }[];
    screenSpaceTotal: THREE.Vector2;
    screenSpaceAreaTotal: number;
    name: string;
    id: number;
    textVisible: boolean;
    widthTotal: number;
    heightTotal: number;
    count: number;
}

export interface Vector3Data {
    x: number;
    y: number;
    z: number;
}

export interface OrbitReelLabelData {
    id: number;
    name: string;
    normal: Vector3Data;
    position: Vector3Data;
    vertices: Vector3Data[];
}

export interface OrbitReelInput {
    isMouseDown: boolean;
    mousePosition: number[];
}

export interface OrbitReelState {
    prebakedLabels: {
        svgLabels: ComputedLabelData[];
        minX: number;
        minY: number;
        maxX: number;
        maxY: number;
    }[];
    isPanEnabled: boolean;
    panZoom?: SvgPanZoom.Instance;
    controlMode: OrbitReelControlMode;
    occlusion?: number[][];
    orbitImageCount: number;
    orbitReelWidth: number;
    orbitReelHeight: number;
    orbitReelAspect: number;
    anglePerImage: number;
    imageRectangle: Rectangle;
    domElements?: OrbitReelStateElements;
    imageStore: SVGImageElement[];
    imageLowResStore: SVGImageElement[];
    options: OrbitReelOptions;
    currentOrbitAngle: number;
    currentOrbitIndex: number;
    currentVisibleImageElement: SVGImageElement;
    currentVisibleImageIndex: number;
    input: OrbitReelInput;
    movementSpeed: number;
    movementDirection: number;
    isMoving: boolean;
    isUserInputLocked: boolean;
    isRenderingLabels: boolean;
    isRenderingFloors: boolean;
    activeTransition?: OrbitReelTransitionData | undefined;
    transitionVideoEnded: boolean;
    moveToFrameTargetIndex: number;
    moveToFrameTargetCallback?(): void;
    shownUnits: IUnit[];
    buildingData: IBuilding;
}

export interface OrbitReelStateElements {
    container: HTMLElement;
    svgElement: SVGSVGElement;
    svgImageGroup: SVGGElement;
    labelGroup: SVGGElement;
    transitionVideoElement: HTMLVideoElement;
    transitionSourceElement: HTMLSourceElement;
    clickableRegionsGroup: SVGGElement;
}

export enum OrbitReelControlMode {
    PAN = "pan",
    ORBIT = "orbit",
}

export interface OrbitReelOptions {
    debugMode?: boolean;
    container: HTMLElement;
    imageWidth: number;
    imageHeight: number;
    orbitImageUrls: string[];
    orbitLowResImageUrls: string[];
    camera?: OrbitReelCameraData;
    labels?: OrbitReelLabelData[];
    thetaChangedCallback?(theta: number): void;
    labelClickedCallback?(label: { name: string }): void;
    labelEnterCallback?(label: { name: string }): void;
    labelLeaveCallback?(label: { name: string }): void;
    floorClickedCallback?(floor: number): void;
    amenityModeCallback?(amenityMode: boolean): void;
    amenityClickedCallback?(name): void;
    amenityRegionClickedCallback?(region: { name: string; url: string }): void;
    transitions?: OrbitReelTransitionData[];
    occlusion?: number[][];
    movementSpeed?: number;
    units: IUnit[];
    building: IBuilding;
    labelFontSize?: number | string;
    useLabelDatabaseColor?: boolean;
    labelColor?: string;
    labelColor1BR?: string;
    labelColor2BR?: string;
    labelColor3BR?: string;
    labelColor4BR?: string;
    hideAvailableUnits?: boolean;
    hideUnavailableUnits?: boolean;
}

export interface ComputedLabelData {
    instructions: string[];
    x: number;
    y: number;
    name: string;
    id: number;
    distance: number;
    textVisible: boolean;
    fontSize: number;
    minX: number;
    maxX: number;
    minY: number;
    maxY: number;
}

export interface OrbitReelTransitionData {
    name: string;
    angle: number;
    videoSource: string;
    videoReverseSource: string;
    posterImageSource?: string;
    clickableRegions?: { name: string; url: string; instructions: string }[];
}

export interface IUnit {
    id?: number;
    name: string;
    bedrooms: number;
    bathrooms: number;
    isUnavailable?: boolean;
    status: string;
    floor: number;
    floorplanName: string;
    floorplanAssetsSrc: string[];
    price: number;
    interiorSquareFeet: number;
    exteriorSquareFeet: number;
}

export interface IViewsData {
    id?: string;
    name: string;
    desc: string;
}

export interface IStatusData {
    id?: string;
    name: string;
    color: string;
    orderId: string;
    externalValue: string;
}

export interface IBuilding {
    id?: string;
    development_id: string;
    name: string;
    lat: string;
    lng: string;
    address: string;
    city: string;
    zip: string;
    floors: string;
    building_views: IViewsData[];
    unit_statuses: IStatusData[];
}

export const dummy = { x: 10 };
