import {
    SageVideoProperties,
    SageNodeController,
    SageNodeType
} from "./types";

export function videoControllerFactory(props: SageVideoProperties): SageNodeController {
    let video: HTMLVideoElement;
    let _goBack: () => void;

    const mount = (container, navigateToNode, goBack) => {
        console.log("[videoController]: MOUNT");

        video = document.createElement("video");
        video.src = props.url;
        video.width = container.clientWidth;
        video.play();
        video.style.cssText = `position: relative;
top: 50%;
transform: translateY(-50%);`;

        video.onended = () => {
            goBack();
        }

        _goBack = goBack;

        container.appendChild(video);
    };

    const onNodePresented = () => {
        if (_goBack) {
            video.onended = () => {
                _goBack();
            }
        }

        video && video.play();
    };

    const onNodeHidden = () => {
        video.onended = undefined;
    };

    return ({
        init: () => { },
        mount,
        getNodeType: () => SageNodeType.VideoNode,
        onNodePresented,
        onNodeHidden
    })
}
